/* transition for the router */
/* tutorial source: https://youtu.be/NUQkajBdnmQ  */

.transition-wrapper {
    position: absolute;
    left: 0;
    right: 0;
}

.fade-enter {
    opacity: 0;
}
.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
}
.fade-exit {
    opacity: 1;
}
.fade-exit-active {
    opacity: 0;
    transition: opacity 300ms ease-out;
}

.title-padding-left {
    text-align: left;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

.title-padding-center {
    text-align: center;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

